import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { fetchUsers } from '../../api/userAPI';
import { RootState } from '../store';

interface User {
  id: number;
  username: string;
  email:string;
  thumbnailUrl: string | null;
}

interface AuthUsersState {
  users: User[]; // Change to an array of users
  loading: boolean;
  error: string | null;
}

const initialState: AuthUsersState = {
  users: [], // Initialize as an empty array
  loading: false,
  error: null,
};

// Updated thunk to fetch all users
export const fetchAuthUsers = createAsyncThunk<User[], void, { state: RootState }>(
  'authUsers/fetchAuthUsers',
  async (_, { getState }) => {
    const state = getState();
    const token = state.auth.token; // Accessing the token from auth state
    if (!token) {
      throw new Error('Token not available');
    }
    const users = await fetchUsers(token);
    // Map the fetched users to the required format
    return users.map((user: { id: number; username: string; email:string; thumbnail: { formats: { thumbnail: { url: string; }; }; }; }) => ({
      id: user.id,
      username: user.username,
      email:user.email,
      thumbnailUrl: user.thumbnail ? user.thumbnail.formats.thumbnail.url : null,
    }));
  }
);

const authUsersSlice = createSlice({
  name: 'authUsers',
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(fetchAuthUsers.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(fetchAuthUsers.fulfilled, (state, action) => {
        state.users = action.payload; // Store all users
        state.loading = false;
      })
      .addCase(fetchAuthUsers.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message || 'Failed to fetch users';
      });
  },
});

export default authUsersSlice.reducer;
