import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { createBooking } from '../redux/auth/authbookingsSlice';
import { RootState, AppDispatch } from '../redux/store';
import { Button } from '../components/ui/button';
import SelectParticipantsModal from '../components/selectussers'; // Correct import path

interface Participant {
  id: number;
  username: string;
  email: string;
}

const CreateBooking: React.FC = () => {
  const [summary, setSummary] = useState('');
  const [location, setLocation] = useState('');
  const [dateTime, setDateTime] = useState('');
  const [participants, setParticipants] = useState<Participant[]>([]);
  const dispatch = useDispatch<AppDispatch>();
  const navigate = useNavigate();
  const auth = useSelector((state: RootState) => state.auth);

  const handleSubmit = async (event: React.FormEvent) => {
    event.preventDefault();
    if (auth.token) {
      const bookingData = {
        data: {
          summary,
          location,
          date_time: dateTime,
          status: 'pending',
          participants: participants.map(p => p.id) // Ensure only IDs are sent
        }
      };
      await dispatch(createBooking({ token: auth.token, bookingData }));
      navigate('/dashboard');
    }
  };

  const handleSaveParticipants = (selectedParticipants: Participant[]) => {
    setParticipants(selectedParticipants);
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-gray-100 p-4">
      <div className="bg-white border border-gray-300 rounded-lg shadow-lg p-8 max-w-md w-full">
        <h2 className="text-2xl font-semibold mb-6">Create Booking</h2>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="space-y-2">
            <label htmlFor="summary" className="block text-sm font-medium text-gray-700">Summary</label>
            <input
              type="text"
              id="summary"
              value={summary}
              onChange={(e) => setSummary(e.target.value)}
              className="block w-full border border-gray-300 rounded-md p-2"
              required
            />
          </div>
          <div className="space-y-2">
            <label htmlFor="location" className="block text-sm font-medium text-gray-700">Location</label>
            <input
              type="text"
              id="location"
              value={location}
              onChange={(e) => setLocation(e.target.value)}
              className="block w-full border border-gray-300 rounded-md p-2"
              required
            />
          </div>
          <div className="space-y-2">
            <label htmlFor="dateTime" className="block text-sm font-medium text-gray-700">Date and Time</label>
            <input
              type="datetime-local"
              id="dateTime"
              value={dateTime}
              onChange={(e) => setDateTime(e.target.value)}
              className="block w-full border border-gray-300 rounded-md p-2"
              required
            />
          </div>
          <div className="space-y-2">
            <label className="block text-sm font-medium text-gray-700">Add Users</label>
            <SelectParticipantsModal
              onSave={handleSaveParticipants}
              selectedParticipants={participants}
            />
          </div>
          <Button type="submit" className="bg-gray-200 text-black hover:bg-gray-300">
            Request Booking
          </Button>
        </form>
      </div>
    </div>
  );
};

export default CreateBooking;
