// src/api/userAPI.ts
import axios, { AxiosError } from "axios";

export const fetchUsers = async (token: string) => {
  const response = await fetch(
    "https://jellyfish-app-43090-zm6h3.ondigitalocean.app/api/users?populate=*",
    {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    }
  );
  if (!response.ok) {
    throw new Error("Failed to fetch users");
  }
  return response.json();
};

export const updateUserName = async (token: string) => {
  const userId = 1; // Replace with the actual user ID
  const newName = "hungcan2003"; // Replace with the new name

  try {
    const response = await axios.put(
      `https://jellyfish-app-43090-zm6h3.ondigitalocean.app/api/users/${userId}`,
      {
        name: newName,
      },
      {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      }
    );

    console.log("User updated:", response.data);
  } catch (error: unknown) {
    if (axios.isAxiosError(error)) {
      console.error(
        "Error updating user:",
        error.response ? error.response.data : error.message
      );
    } else {
      console.error("Unknown error occurred:", (error as Error).message);
    }
  }
};
