import axios from 'axios';

export const fetchBookings = async (token: string) => {
  const response = await axios.get('https://jellyfish-app-43090-zm6h3.ondigitalocean.app/api/bookings?populate=*', {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });
  return response.data;
};

export const fetchBookingDetails = async (token: string, bookingId: number) => {
  const response = await axios.get(`https://jellyfish-app-43090-zm6h3.ondigitalocean.app/api/bookings/${bookingId}`, {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });
  return response.data;
};

export const createBooking = async (token: string, bookingData: any) => {
  const response = await axios.post('https://jellyfish-app-43090-zm6h3.ondigitalocean.app/api/bookings?populate=*', bookingData, {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });
  return response.data;
};

export const deleteBooking = async (token: string, bookingId: number) => {
  await axios.delete(`https://jellyfish-app-43090-zm6h3.ondigitalocean.app/api/bookings/${bookingId}`, {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  });
};

// In api/bookingsApi.ts (or similar file)
export const apiUpdateBooking = async (token: string, bookingId: number, bookingData: any) => {
  const response = await axios.put(
    `https://jellyfish-app-43090-zm6h3.ondigitalocean.app/api/bookings/${bookingId}`,
    bookingData,
    {
      headers: {
        Authorization: `Bearer ${token}`,
        'Content-Type': 'application/json',
      },
    }
  );
  return response.data;
};
