"use client";
import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { RootState } from '../redux/store'; // Ensure this path is correct
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "../components/ui/popover";
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
} from "../components/ui/command";
import { Check, ChevronsUpDown } from "lucide-react";
import { cn } from "../lib/utils";

interface Participant {
  id: number;
  username: string;
  email: string;
}

interface SelectParticipantsModalProps {
  onSave: (selectedParticipants: Participant[]) => void;
  selectedParticipants: Participant[];
}

const SelectParticipantsModal: React.FC<SelectParticipantsModalProps> = ({
  onSave,
  selectedParticipants,
}) => {
  // Access users from Redux state
  const users = useSelector((state: RootState) => state.authUser.users || []); // Default to empty array
  const [participants, setParticipants] = useState<Participant[]>(users);
  const [selected, setSelected] = useState<Participant[]>(selectedParticipants || []);
  const [open, setOpen] = useState(false);
  const [value, setValue] = useState("");

  // Update participants when users state changes
  useEffect(() => {
    setParticipants(users);
  }, [users]);

  const handleSelectParticipant = (participant: Participant) => {
    const updatedSelected = selected.some((p) => p.id === participant.id)
      ? selected.filter((p) => p.id !== participant.id)
      : [...selected, participant];

    setSelected(updatedSelected);
    onSave(updatedSelected);
  };

  const handleRemoveParticipant = (participant: Participant) => {
    const updatedSelected = selected.filter((p) => p.id !== participant.id);
    setSelected(updatedSelected);
    onSave(updatedSelected);
  };

  return (
    <div>
      <Popover open={open} onOpenChange={setOpen}>
        <PopoverTrigger asChild>
          <button
            role="combobox"
            aria-expanded={open}
            aria-controls="participant-combobox"
            className="w-full flex justify-between bg-gray-100 border border-gray-300 rounded-md p-2"
          >
            {value
              ? participants.find((participant) => participant.username === value)?.username
              : "Select participant..."}
            <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
          </button>
        </PopoverTrigger>
        <PopoverContent id="participant-combobox" className="w-full p-0">
          <Command>
            <CommandInput placeholder="Search participant..." />
            <CommandEmpty>No participant found.</CommandEmpty>
            <CommandGroup>
              {participants.map((participant) => (
                <CommandItem
                  key={participant.id}
                  value={participant.username}
                  onSelect={(currentValue: React.SetStateAction<string>) => {
                    const selectedParticipant = participants.find(
                      (p) => p.username === currentValue
                    );
                    if (selectedParticipant) {
                      handleSelectParticipant(selectedParticipant);
                    }
                    setValue(currentValue);
                    setOpen(false);
                  }}
                >
                  <Check
                    className={cn(
                      "mr-2 h-4 w-4",
                      selected.some((p) => p.username === participant.username)
                        ? "opacity-100"
                        : "opacity-0"
                    )}
                  />
                  {participant.username}
                </CommandItem>
              ))}
            </CommandGroup>
          </Command>
        </PopoverContent>
      </Popover>
      <div className="flex flex-wrap gap-2 mt-2">
        {selected.map((participant) => (
          <div
            key={participant.id}
            className="bg-green-100 text-green-700 flex items-center space-x-2 px-3 py-1 rounded-full"
          >
            <span>{participant.username}</span>
            <button
              onClick={() => handleRemoveParticipant(participant)}
              className="text-red-500 hover:text-red-700"
            >
              
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default SelectParticipantsModal;
